/* BodySection.css */
.body {
  text-align: center;
  width: "1200px";
  height: "350px";
}

.content-box {
  background-color: #ff0000; /* Red background color */
  padding: 20px;
  margin: 20px auto; /* Center the box horizontally */
  max-width: 1200px; /* Set a maximum width for the box */
}

.button-container {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.action-button {
  background-color: #812ec5;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

.action-button:hover {
  background-color: #033b18;
}
.first-para {
  background-color: rgb(244, 242, 234);
}
.coloredone {
  color: blueviolet;
}
.colored2 {
  color: cadetblue;
}
.colored3 {
  color: coral;
}
.explore-button {
  background-color: #ff0066;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  margin-top: 20px;
  cursor: pointer;
}

.explore-button:hover {
  background-color: #0056b3;
}
.unique {
  color: #10227d;
  text-shadow: #c5c32e;
}
