/* Header.css */
.header {
  background-color: #f0f0f0;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: start;
}

.logo {
  font-size: 1.5rem;
}

.navigation {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.nav-button {
  background-color: #262ec0;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  width: 250px;
}

/* Add hover effect if desired */
.nav-button:hover {
  background-color: #d93883;
}
.intro-container {
  text-align: left;
}

.family-wellness {
  color: red;
}

.massage-therapy {
  color: black;
}
